.transform-item {
    width: 90%;
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 10px;
}
  
.transform-header {
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.transform-item:hover {
    background-color: #f0f0f0;
}

.name {
flex-grow: 1;
}

.icons {
display: flex;
align-items: center;
}

.edit-icon,
.delete-icon {
margin-left: 10px;
cursor: pointer;
}

.highlighted {
    background: linear-gradient(45deg, #b1e8c4, #9fd8b5); /* Gradient background */
    animation: pulsate 1s ease-in-out infinite; /* Pulsating animation */
  }
  
@keyframes pulsate {
0% {
    transform: scale(1);
}
50% {
    transform: scale(1.05);
}
100% {
    transform: scale(1);
}
}
  
.close-button {
background: none;
border: none;
color: #999;
font-size: 18px;
cursor: pointer;
}

.close-button:hover {
color: #666;
}

.matrix {
margin-top: 10px;
}

.row {
display: flex;
align-items: center;
margin-bottom: 5px;
}

input[type='text'] {
width: 40px;
padding: 5px;
border: 1px solid #ccc;
border-radius: 4px;
margin-right: 5px;
text-align: center;
}

.transform-item {
    position: relative;
}

.transform-actions {
position: absolute;
top: 0;
right: -40px;
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
width: 40px;
height: 100%;
opacity: 0;
transition: opacity 0.2s;
}

.transform-item:hover .transform-actions {
opacity: 1;
}

.move-button {
display: flex;
align-items: center;
justify-content: center;
width: 30px;
height: 30px;
border: none;
background-color: transparent;
font-size: 20px;
cursor: pointer;
}
  
  